import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import {product} from './../models/product';
import {productService} from './../services/product.service';
import { FormControl, FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {

  products:product[]=[];
  product:product;
  loading: boolean = false;

  @ViewChild('fileInput') fileInput: ElementRef;

  dbForm:FormGroup=this.builder.group({//^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$
    name:new FormControl('Randhir'),
    st_date:new FormControl(null),
    description:new FormControl('randhir'),
    price:new FormControl(450),
    avatar: null
  });
  constructor(private builder:FormBuilder,private dataservice:productService) { 
    this.product = new product();
  }

  ngOnInit() {
    this.getdata();
  }
  getdata(){
    this.dataservice.get().subscribe(data=>{this.products=data;
    console.log(data);
    });
  }
  save(){
    console.log(this.dbForm.value);
    this.dataservice.save(this.dbForm.value).subscribe(data=>this.getdata());

    // const formModel = this.dbForm.value;
    // this.loading = true;
    // // In a real-world app you'd have a http request / service call here like
    // // this.http.post('apiUrl', formModel)
    // setTimeout(() => {
    //   console.log(formModel);
    //   alert('done!');
    //   this.loading = false;
    // }, 1000);
  }

  onFileChange(event) {
    let reader = new FileReader();
    if(event.target.files && event.target.files.length > 0) {
      let file = event.target.files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        console.log(reader.result);
        
        this.dbForm.get('avatar').setValue({
          filename: file.name,
          filetype: file.type,
          value: reader.result.split(',')[1]
        })
      };
    }
  }
clearFile() {
    this.dbForm.get('avatar').setValue(null);
    this.fileInput.nativeElement.value = '';
  }
}



